.container {
    width: 80%;
    margin: 50px auto;
}

textarea {
    width: 400px;
    height: 100px;
    background: #eee;
    padding: 10px;
    border-radius: 10px;
}

.word-count {
    color: blue;
    font-weight: bold;
}

.char-count {
    color: red;
    font-weight: bold;
}